import axios from "axios";
import { db } from "../firebaseSetup";
import {collection, addDoc, Timestamp} from 'firebase/firestore'

const AddNewItems = () => {

    const formSubmit= async(e)=>{
        e.preventDefault();
        let body ={
            itemName:e.target.itemName.value,
            price:e.target.productAmount.value,
            date:e.target.purchaseDate.value,
            description:e.target.description.value,
            created: Timestamp.now()
        }
        try {
            await addDoc(collection(db,'expenseTracker'),body)
          } 
        catch (err) {
            alert(err)
          }     
    }

  return (
    <div id="exampleModal" tabIndex="-1" className="modal fade">
      <div className="modal-dialog modal-dialog-centered modal-dialog-scrollable">
        <div className="modal-content">
        <div className="modal-header">
        <h1 className="modal-title fs-5" id="exampleModalLabel">New Item</h1>
        <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
      </div>
      <form className="form-floating" onSubmit={formSubmit}>
      <div className="modal-body">
        
          <div className="form-floating mb-3">
          <input type="text" className="form-control" id="itemName" name="productName" />
         <label htmlFor="itemName">product name</label>
          </div>
          <div className="form-floating">
          <textarea className="form-control" placeholder="for memory" id="description" style={{"height":"80px"}} name="description"></textarea>
          <label htmlFor="description">Description</label>
          </div>
           <div className="form-floating" style={{"marginTop":"10px"}}>
           <input type="date" className="form-control" id="date" name="purchaseDate"/>
           <label htmlFor="date">product name</label>
          </div>
          <div className="form-floating" style={{"marginTop":"10px"}}>
          <input type="number" className="form-control" name="productAmount" id="productprice"/>
         <label htmlFor="productprice">product price</label>
         </div>
       
      </div>
      <div className="modal-footer">
        <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
        <button type="submit" className="btn btn-primary">submit</button>
      </div>
      </form>

        </div>
      </div>
    </div>
  );
};
export default AddNewItems;
