import DisplayItems from './components/DisplayItems';
import AddNewItems from './components/AddNewItem';
import './App.css';

function App() {
  return <div className="container" style={{"marginTop":"20px"}}>
    <AddNewItems id="exampleModal" tabIndex="-1" aria-labelledby="exampleModalLabel"/>
    <div className="row" style={{"marginTop":"40px"}}>
      <div className="col-12">
      <button type="button" className="btn" data-bs-toggle="modal" data-bs-target="#exampleModal" style={{"backgroundColor":"#212529","color":"white","float":"right","marginRight":"20px"}}>add</button>
      </div>
      <div className="col-12">
      <DisplayItems/>
      </div>
      
    </div>
  </div>
}

export default App;
