import { useEffect, useState } from "react";
import {
  collection,
  query,
  orderBy,
  onSnapshot,
  deleteDoc,
  doc,
} from "firebase/firestore";
import { db } from "../firebaseSetup";

const DisplayItems = () => {
  const [totalSpent, setTotalSpent] = useState();
  const [Data, setData] = useState([]);

  useEffect(() => {
    const q = query(
      collection(db, "expenseTracker"),
      orderBy("created", "desc")
    );
    onSnapshot(q, (querySnapshot) => {
      setData(
        querySnapshot.docs.map((doc) => ({
          id: doc.id,
          data: doc.data(),
        }))
      );
    });
  }, []);
  useEffect(() => {
    let totalVal = 0;
    Data.forEach((element) => {
      totalVal = totalVal + Number(element.data["price"]);
    });
    setTotalSpent(totalVal);
  }, [Data]);

  async function deleteItem(x) {
    await deleteDoc(doc(db, "expenseTracker", x));
  }
  const removeItem = (e, x) => {
    e.preventDefault();
    deleteItem(x.id);
  };

  return (
    <div className="container" style={{ marginTop: "10px" }}>
      {Data.length >= 1 && (
        <div className="row align-items-center">
          <div className="col-xs-1-12">
            <table className="table table-dark table-hover">
              <thead>
                <tr>
                  <th scope="col">count</th>
                  <th scope="col">Name</th>
                  <th scope="col">Date</th>
                  <th scope="col">price</th>
                  <th scope="col">options</th>
                </tr>
              </thead>
              <tbody>
                {Data.map((element, index) => {
                  return (
                    <tr key={index + 1}>
                      <td scope="col">{index + 1}</td>
                      <td>{element.data.itemName}</td>
                      <td>{new Date(element.data.date).toDateString()}</td>
                      <td>{element.data.price}</td>
                      <td>
                        <i
                          className="bi bi-trash3"
                          style={{ cursor: "pointer" }}
                          onClick={(event) => removeItem(event, element)}
                        ></i>
                      </td>
                    </tr>
                  );
                })}
                <tr>
                  <td></td>
                  <td colSpan="2">Total</td>
                  {totalSpent ? <td colSpan="2">{totalSpent}</td>:<td></td>}
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      )}
    </div>
  );
};
export default DisplayItems;
